import React, { HTMLAttributes } from "react";
import classNames from "classnames/bind";

export type TitleProps = {
  level?: 1 | 2 | 3 | 4;
};

type HeadProps = HTMLAttributes<HTMLHeadElement>;

export const Title: React.FC<HeadProps & TitleProps> = (props) => {
  const { level, className, children, ...headProps } = props;

  if (level === 2) {
    return (
      <h2 className={classNames("text-white text-center text-3xl md:text-5xl mb-6", className)} {...headProps}>
        {children}
      </h2>
    );
  }

  if (level === 3) {
    return (
      <h3 className={classNames("text-white text-center text-2xl md:text-4xl mb-5", className)} {...headProps}>
        {children}
      </h3>
    );
  }

  if (level === 4) {
    return (
      <h4 className={classNames("text-white text-center text-xl md:text-3xl mb-4", className)} {...headProps}>
        {children}
      </h4>
    );
  }

  return (
    <h1 className={classNames("text-white text-center text-4xl md:text-6xl mb-8", className)} {...headProps}>
      {children}
    </h1>
  );
};
