import React from 'react';
import loadable from '@loadable/component';
import { DAppProvider, Mainnet, Rinkeby, Hardhat } from '@usedapp/core';
import { getDefaultProvider } from 'ethers';
import { Header } from './components/Header';
import { Route, Routes } from 'react-router-dom';
import { Footer } from './components/Footer';
import { Title } from './components/Typography';
import { Loading } from './components/Loading';
import { DegenConfigProvider } from './hooks/useDegenConfig';
import { QueryClient, QueryClientProvider } from 'react-query';
import { ToastContainer } from 'react-toastify';

function createLoadable(thunk: () => Promise<React.FC | { default: React.FC }>) {
  const LoadableComponent = loadable(() => thunk(), {
    fallback: (
      <div className="h-screen flex justify-center items-center text-white py-20 bg-[url(/public/assets/home/bg-degen-text.svg)] bg-no-repeat bg-center bg-contain">
        <Title className="font-bold">
          <Loading />
        </Title>
      </div>
    ),
  });
  return <LoadableComponent />;
}

function App() {
  const queryClient = new QueryClient();

  return (
    <QueryClientProvider client={queryClient}>
      <DegenConfigProvider>
        <DAppProvider
          config={{
            readOnlyChainId: Mainnet.chainId,
            readOnlyUrls: {
              [Mainnet.chainId]: getDefaultProvider('mainnet'),
              [Rinkeby.chainId]: getDefaultProvider('rinkeby'),
              // [Hardhat.chainId]: getDefaultProvider('http://localhost:8545'),
            },
          }}
        >
          <div className="App">
            <Header />
            <main className="min-h-[calc(100vh-18rem-64px)] md:min-h-[calc(100vh-20rem-64px)]">
              <Routes>
                <Route path="/" element={createLoadable(() => import('./views/Home'))} />
                <Route path="/roadmap/*" element={createLoadable(() => import('./views/Roadmap'))} />
                <Route path="/about/*" element={createLoadable(() => import('./views/About'))} />
                <Route path="/mint/*" element={createLoadable(() => import('./views/Mint'))} />
              </Routes>
              <ToastContainer />
            </main>
            {/*<Footer />*/}
          </div>
        </DAppProvider>
      </DegenConfigProvider>
    </QueryClientProvider>
  );
}

export default App;
