import React, { useContext } from 'react';

interface DegenConfig {
  chainId: number;
}

const context = React.createContext<null | DegenConfig>(null);

export const useDegenConfig = (): DegenConfig => {
  const config = useContext(context);
  if (!config) {
    throw new Error('useDegenConfig must be used within a DegenProvider');
  }

  return config;
};

export const DegenConfigProvider: React.FC = (props) => {
  const { children } = props;
  return <context.Provider value={{ chainId: Number(process.env.REACT_APP_CHAIN_ID) }}>{children}</context.Provider>;
};
